import React, { Component } from 'react'
import PropTypes from 'prop-types'
import Link from 'next/link'

export default class extends Component {
  static propTypes = {
    q: PropTypes.number
  }

  constructor (props) {
    super(props);
    this.state = {
        ...this.props,
        next: this.props.q + 1
    };
  }

  componentDidMount() {}

  failure() {
      return('Invalid input')
  }

  success() {
    return (
        <div>
            <div>Here goes the question number { this.state.q }, I guess?</div>
            <div>User's Answer</div>
            <div>Unselected Answer</div>
            <div>Unselected Answer</div>
            <div>Result</div>

            <div>Descriptive reasoning...</div>

            <Link href='answer' as={ "/answer/" + this.state.next }>
            <button>Next</button>
            </Link>
        </div>
    )
  }

  render() {
      return(this.success())
  }
}
